import React, { useEffect, useState } from 'react'
import { Trabajos } from './trabajos';
import { Modal } from 'react-bootstrap';
const images = require.context('../../assets/img/', true);
function Portafolio(props) {
    const [modal, setModal] = useState(false)
    const [proyectoSelect, setProyectoSelect] = useState(false)
    useEffect(() => {
        setTimeout(() => {
            if (document.getElementById("script")) {
                document.getElementById("script").remove();
            }
            const script = document.createElement("script");
            script.src = "/js/scripts.js";
            script.id = "script";
            script.async = true;
            document.body.appendChild(script);
        }, 3000);

    }, [])

    return (
        <section className="page-section portfolio" id="portfolio">
            <div className="container">
                <h2 className="page-section-heading text-center text-uppercase text-secondary mb-0">Portfolio</h2>
                <div className="divider-custom">
                    <div className="divider-custom-line"></div>
                    <div className="divider-custom-icon"><i className="fas fa-star"></i></div>
                    <div className="divider-custom-line"></div>
                </div>
                <div className="row justify-content-center">
                    {Trabajos &&
                        Trabajos.map((item) => {
                            return (<div key={item.id} className="col-md-6 col-lg-4 mb-5">
                                <div className="portfolio-item mx-auto" onClick={() => { setProyectoSelect(item); setModal(true) }}>
                                    <div className="portfolio-item-caption d-flex align-items-center justify-content-center h-100 w-100">
                                        <div className="portfolio-item-caption-content text-center text-white"><i className="fas fa-plus fa-3x"></i></div>
                                    </div>
                                    <img className="img-fluid" src={images(`./${item.img[0]}`)} alt="" />
                                </div>
                            </div>)
                        })}
                </div>
            </div>
            <Modal show={modal} onHide={() => { }} onExit={() => { }} dialogClassName="modal-dialog modal-xl">
                <Modal.Header className="text-center">
                    <Modal.Title className="text-center">Proyecto {proyectoSelect.titulo}</Modal.Title>
                    <button type="button" className="close" onClick={() => { setModal(false); setProyectoSelect(false) }}>
                        <i aria-hidden="true" className="fas fa-times fa-fw"></i>
                    </button>
                </Modal.Header>
                <Modal.Body className="text-center">
                    {proyectoSelect &&
                        <div className="container">
                            <div className="row justify-content-center">
                                <div className="col-lg-8">
                                    <h2 className="portfolio-modal-title text-secondary text-uppercase mb-0" id="portfolioModal1Label">{proyectoSelect.sistema}</h2>
                                    <div className="divider-custom">
                                        <div className="divider-custom-line"></div>
                                        <div className="divider-custom-icon"><i className="fas fa-star"></i></div>
                                        <div className="divider-custom-line"></div>
                                    </div>
                                    <img className="img-fluid rounded mb-5" src={images(`./${proyectoSelect.img[0]}`)} alt="" />
                                    <p className="mb-5">{proyectoSelect.descripcion}</p>
                                    {proyectoSelect.url &&
                                        <a className="btn btn-primary mr-2" href={proyectoSelect.url} target="__blank">
                                            <i className="fas fa-globe mr-2"></i>
                                        Ir al sitio Web
                                    </a>
                                    }
                                    {proyectoSelect.android &&
                                        <a className="btn btn-primary mr-2" href={proyectoSelect.android} target="__blank">
                                            <i className="fab fa-google-play mr-2"></i>
                                        Ir a Google Play
                                    </a>
                                    }
                                </div>
                            </div>
                        </div>}
                </Modal.Body>
            </Modal>
        </section>
    )

} export default Portafolio;